<script setup lang="ts">
import { onMounted, ref, watch, computed } from 'vue'
import { useApplicationStore } from '@/stores/application-store'
import { useAuthStore } from '@/stores/auth-store'
import { useToast } from 'vuestic-ui'
import api from '@/services/api'
import axios from '@/services/axios'

const store = useApplicationStore()
const auth = useAuthStore()
const toast = useToast()

const submitError = ref(false)
const submitSuccess = ref(false)

const countryOptions = ref<{ label: string; value: number }[]>([])
const regionOptions = ref<{ label: string; value: number }[]>([])
const districtOptions = ref<{ label: string; value: number }[]>([])
const diplomRegionOptions = ref<{ label: string; value: number }[]>([])
const diplomDistrictOptions = ref<{ label: string; value: number }[]>([])
const allRegions = ref<{ label: string; value: number; countryId: number }[]>([])

const isReadonly = computed(() => store.passport.readonly === true)
const isFetchingPassport = ref(false)

const fetchPassportInfo = async () => {
  isFetchingPassport.value = true
  try {
    await store.fetchPassportInfo()
    if (store.passport.region) await loadDistricts(store.passport.region)
  } catch (err) {
    toast.init({
      title: '‚ùå Qidiruv xatosi',
      message: 'Ma ºlumot topilmadi yoki xatolik yuz berdi.',
      color: 'danger',
    })
  } finally {
    isFetchingPassport.value = false
  }
}

const loadCountries = async () => {
  try {
    const res = await axios.get(api.listCountries())
    const data = res.data.results || res.data
    countryOptions.value = data.map((c: any) => ({ label: c.name, value: Number(c.id) }))
  } catch (e) {
    console.error('Davlatlar yuklanmadi:', e)
  }
}

const loadRegions = async () => {
  try {
    const res = await axios.get(api.listRegions())
    const data = res.data.results || res.data
    allRegions.value = data.map((r: any) => ({
      label: r.name,
      value: Number(r.id),
      countryId: Number(r.country?.id),
    }))
    diplomRegionOptions.value = allRegions.value // bir xil regionlar ishlatiladi
  } catch (e) {
    console.error('Viloyatlar yuklanmadi:', e)
  }
}

const loadDistricts = async (regionId: number) => {
  try {
    const res = await axios.get(api.listDistricts(), { params: { region: regionId } })
    const data = res.data.results || res.data
    districtOptions.value = data.map((d: any) => ({
      label: d.name,
      value: Number(d.id),
    }))
  } catch (e) {
    console.error('Tumanlar yuklanmadi:', e)
  }
}

const loadDiplomDistricts = async (regionId: number) => {
  try {
    const res = await axios.get(api.listDistricts(), { params: { region: regionId } })
    const data = res.data.results || res.data
    diplomDistrictOptions.value = data.map((d: any) => ({
      label: d.name,
      value: Number(d.id),
    }))
  } catch (e) {
    console.error('Diplom uchun tumanlar yuklanmadi:', e)
  }
}

watch(
  () => store.passport.country,
  (countryId) => {
    const parsedId = Number(countryId)
    if (!isNaN(parsedId)) {
      store.passport.region = null
      store.passport.district = null
      regionOptions.value = allRegions.value.filter((r) => r.countryId === parsedId)
    } else {
      regionOptions.value = []
    }
  },
  { immediate: true },
)

watch(
  () => store.passport.region,
  (newRegionId) => {
    if (newRegionId) loadDistricts(Number(newRegionId))
    else districtOptions.value = []
  },
)

watch(
  () => store.diplomForm.region,
  (newRegionId) => {
    if (newRegionId) loadDiplomDistricts(Number(newRegionId))
    else diplomDistrictOptions.value = []
  },
)



const nextStep = async () => {
  try {
    submitError.value = false
    submitSuccess.value = false

    if (store.step === 'passport' && !store.passport.readonly) {
      toast.init({
        title: 'Passport to‚Äòldirilmagan',
        message: 'Iltimos, passport ma ºlumotlarini to‚Äòldiring yoki avtomatik yuklang.',
        color: 'warning',
      })
      return
    }

    // ‚¨áÔ∏è Oldingi step
    if (store.step === 'passport') {
      await store.submitPassport()
      store.setStep('diplom')
    } else if (store.step === 'diplom') {
      await store.saveDiplom()
      store.setStep('application')
    } else if (store.step === 'application') {
      await store.submitApplication()
      submitSuccess.value = true
    }
  } catch (e) {
    submitError.value = true
    toast.init({
      message: 'Xatolik yuz berdi. Qayta urinib ko‚Äòring.',
      title: '‚ùå Xatolik',
      color: 'danger',
    })
  }
}

