console.log(coursesOptions)

onMounted(async () => {
  await loadCountries()
  await loadRegions()

  await auth.fetchUser()

  if (store.passport.region) await loadDistricts(store.passport.region)
  if (auth.user?.profile) {
    const p = auth.user.profile
    store.passport = {
      series: p.passport_series,
      number: p.number,
      birth_date: p.birth_date,
      full_name: `${p.last_name} ${p.first_name} ${p.other_name}`.trim(),
      address: p.address,
      country: p.country,
      region: p.region,
      district: p.district,
      pinfl: p.pinfl,
      image: p.image,
      readonly: true,
    }
    if (p.region) await loadDistricts(p.region)
    store.setStep('diplom')
  }
  if (auth.user?.diplom) {
    const d = auth.user.diplom
    store.diplomForm = {
      serial_number: d.serial_number,
      education_type: d.education_type,
      institution_type: d.institution_type,
      university_name: d.university_name,
      graduation_year: d.graduation_year,
      region: d.region,
      district: d.district,
      diploma_file: null,
      readonly: true,
    }
    store.diplomType = 'regular'
    store.setStep('application') // Passport va Diplom o‚Äòtgani uchun
  }
  if (auth.user?.diplom) {
    store.application.diplom = auth.user.diplom.id
  }
  if (auth.user?.transfer_diplom) {
    store.application.transfer_diplom = auth.user.transfer_diplom.id
  }
  branchOptions.value = (await axios.get(api.listBranches())).data
  educationLevelOptions.value = (await axios.get(api.listEducationLevels())).data
  educationFormOptions.value = (await axios.get(api.listEducationForms())).data
  programOptions.value = (await axios.get(api.listPrograms())).data
  coursesOptions.value = (await axios.get(api.listCourses())).data
  educationTypeOptions.value = (await axios.get(api.listEducationTypes())).data
  institutionTypeOptions.value = (await axios.get(api.listIstitutionTypes())).data

  const res = await axios.get(api.listCourses())
  coursesOptions.value = res.data.results || res.data
})
