// stores/application-store.ts
import { defineStore } from 'pinia'
import { useAuthStore } from './auth-store'
import axios from '@/services/axios'
import api from '@/services/api'

export const useApplicationStore = defineStore('application', {
  state: () => ({
    step: 'passport' as 'passport' | 'diplom' | 'application',
    hasSubmitted: false,
    diplomType: null as 'regular' | 'transfer' | null,

    passport: {
      series: '',
      number: '',
      birth_date: '',
      full_name: '',
      address: '',
      country: null as number | null,
      region: null as number | null,
      district: null as number | null,
      pinfl: '',
      image: null as File[] | string | null,
      gender: '',
      nationality: '',
      readonly: false,
    },

    diplomForm: {
      serial_number: '',
      education_type: null,
      institution_type: null,
      university_name: '',
      graduation_year: '',
      region: null,
      district: null,
      diploma_file: [] as File | null,
      readonly: false,
    },

    transferDiplomForm: {
      country: null,
      university_name: '',
      target_course: null,
      transcript_file: [] as File | null,
    },

    application: {
      admission_type: '',
      branch: null,
      education_level: null,
      education_form: null,
      program: null,
      diplom: null,
      transfer_diplom: null,
    },
  }),

  actions: {
    setStep(step: 'passport' | 'diplom' | 'application') {
      this.step = step
    },

    async fetchPassportInfo() {
      const auth = useAuthStore()
      const { series, number, birth_date } = this.passport
      const res = await axios.get(api.getPassportInfo(), {
        params: { series, number, birth_date },
        headers: { Authorization: `Bearer ${auth.accessToken}` },
      })
      const data = res.data
      this.passport.full_name = data.full_name || ''
      this.passport.address = data.address || ''
      this.passport.region = Number(data.region_id) || null
      this.passport.district = Number(data.district_id) || null
      this.passport.readonly = true
      this.passport.image = data.photo_url || ''
      this.passport.pinfl = data.passport_number || ''
    },

    async submitPassport() {
      try {
        const formData = new FormData()
        formData.append('birth_date', this.passport.birth_date)
        formData.append('passport_series', this.passport.series)
        formData.append('pinfl', this.passport.pinfl)

        const [lastName, firstName, ...others] = this.passport.full_name.split(' ')
        formData.append('last_name', lastName || '')
        formData.append('first_name', firstName || '')
        formData.append('other_name', others.join(' ') || '')

        if (this.passport.address) formData.append('address', this.passport.address)
        if (this.passport.country) formData.append('country', String(this.passport.country))
        if (this.passport.region) formData.append('region', String(this.passport.region))
        if (this.passport.district) formData.append('district', String(this.passport.district))
        if (this.passport.gender) formData.append('gender', this.passport.gender)
        if (this.passport.nationality) formData.append('nationality', this.passport.nationality)

        if (this.passport.image instanceof File) {
          formData.append('image', this.passport.image)
        } else if (typeof this.passport.image === 'string') {
          const file = await fetch(this.passport.image)
            .then((res) => res.blob())
            .then((blob) => new File([blob], 'passport.jpg', { type: blob.type }))
          formData.append('image', file)
        }

        const res = await axios.post(api.createProfile(), formData, {
          headers: { 'Content-Type': 'multipart/form-data' },
        })

        console.log('‚úÖ Passport saqlandi:', res.data)
      } catch (error: any) {
        console.error('‚ùå Passport saqlashda xato:', error.response?.data || error)
        throw new Error(
          error.response?.data?.non_field_errors?.[0] ||
            error.response?.data?.passport_series?.[0] ||
            'Passport ma ºlumotlari yuborilmadi',
        )
      }
    },

    async saveDiplom() {
      try {
        const auth = useAuthStore()

        if (this.diplomType === 'regular') {
          const formData = new FormData()
          formData.append('serial_number', this.diplomForm.serial_number)
          formData.append('education_type', String(this.diplomForm.education_type))
          formData.append('institution_type', String(this.diplomForm.institution_type))
          formData.append('university_name', this.diplomForm.university_name)
          formData.append('graduation_year', this.diplomForm.graduation_year)
          formData.append('region', String(this.diplomForm.region))
          formData.append('district', String(this.diplomForm.district))

          if (this.diplomForm.diploma_file instanceof File) {
            formData.append('diploma_file', this.diplomForm.diploma_file)
          }

          const res = await axios.post(api.createDiplom(), formData, {
            headers: { 'Content-Type': 'multipart/form-data' },
          })

          this.application.diplom = res.data.id
          auth.user.diplom = res.data
          return res.data.id // üõ† return qilingan
        }

        if (this.diplomType === 'transfer') {
          const formData = new FormData()
          formData.append('country', String(this.transferDiplomForm.country))
          formData.append('university_name', this.transferDiplomForm.university_name)
          formData.append('target_course', String(this.transferDiplomForm.target_course))

          if (this.transferDiplomForm.transcript_file instanceof File) {
            formData.append('transcript_file', this.transferDiplomForm.transcript_file)
          }

          const res = await axios.post(api.createTransferDiplom(), formData, {
            headers: { 'Content-Type': 'multipart/form-data' },
          })

          this.application.transfer_diplom = res.data.id
          auth.user.transfer_diplom = res.data
          return res.data.id // üõ† return qilingan
        }
      } catch (error: any) {
        console.error('‚ùå Diplom saqlashda xato:', error.response?.data || error)
        throw new Error(
          error.response?.data?.non_field_errors?.[0] ||
            Object.values(error.response?.data || {}).flat()?.[0] ||
            'Diplom ma ºlumotlari yuborilmadi',
        )
      }
    },
    async submitApplication() {
      console.log('üîç Funksiya ishga tushdi') // 1-qadam
      console.log(this.diplomForm)

      // Diplomlar yo'qligini aniqlash
      if (!this.application.diplom && !this.application.transfer_diplom) {
        toast.init({
          title: '‚ö†Ô∏è E ºtibor',
          message: 'Iltimos, diplom yoki transfer diplom ma ºlumotlarini kiriting.',
          color: 'warning',
        })
        return
      }

      const payload: Record<string, any> = {
        admission_type: this.application.admission_type,
        branch: this.application.branch,
        education_level: this.application.education_level,
        education_form: this.application.education_form,
        program: this.application.program,
      }

      // Faqat bir turdagi diplom qo‚Äòshiladi
      if (this.diplomType === 'regular' && Number(this.application.diplom)) {
        payload.diplom = Number(this.application.diplom)
      } else if (this.diplomType === 'transfer' && Number(this.application.transfer_diplom)) {
        payload.transfer_diplom = Number(this.application.transfer_diplom)
      }

      try {
        const res = await axios.post(api.createApplication(), payload)
        this.hasSubmitted = true

        console.log('‚úÖ Application yuborildi:', res.data)
        console.log('üì§ Application payload:', payload)
        console.log('üìé Diplom ID:', this.application.diplom)
        console.log('üìé Transfer Diplom ID:', this.application.transfer_diplom)
      } catch (error: any) {
        console.error('‚ùå Application yuborishda xato:', error.response?.data || error)

        toast.init({
          title: '‚ùå Xatolik',
          message: JSON.stringify(error.response?.data || error),
          color: 'danger',
        })

        throw new Error(
          error.response?.data?.non_field_errors?.[0] ||
            Object.values(error.response?.data || {}).flat()?.[0] ||
            'Application yuborilmadi',
        )
      }
    },

    async nextStep() {
      if (this.step === 'passport') {
        await this.submitPassport()
        this.setStep('diplom')
      } else if (this.step === 'diplom') {
        const diplomId = await this.saveDiplom()

        // üõ† Bu yerda return qilinayotgan ID ni to‚Äòg‚Äòri set qilish kerak
        if (this.diplomType === 'transfer') {
          this.application.transfer_diplom = diplomId
        } else if (this.diplomType === 'regular') {
          this.application.diplom = diplomId
        }

        this.setStep('application')
      } else if (this.step === 'application') {
        await this.submitApplication()
      }
    },
    prevStep() {
      if (this.step === 'application') this.step = 'diplom'
      else if (this.step === 'diplom') this.step = 'passport'
    },
  },
})
